import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { compose } from 'redux'
import { withAuthRedirect } from '../../hoc/withAuthRedirect'
import { setProfileThunk, setStatus, updateStatus } from '../../redux/profile_reducer'
import Profile from './Profile'

class ProfileContainer extends React.Component {
    componentDidMount() {
        let userId = this.props.match.params.userId || this.props.meId
        if(!userId){
            this.props.history.push('/login')
        }
        this.props.setProfileThunk(userId)
        this.props.setStatus(userId)
    }
    render() {

        return <Profile 
        {...this.props} 
        status={this.props.profilePage.status} 
        updateStatus={this.props.updateStatus} />

    }
}

let mapStateToProps = (state) => ({
    profilePage: state.profilePage,
    meId: state.auth.userId
})


// передаем второй параметр через все обработчики первого параметра, начиная с последнего
// with Router прокидывает пропсы далее в компоненты
export default compose(
    connect(mapStateToProps, { setProfileThunk, setStatus, updateStatus }),
    withRouter,
    withAuthRedirect)
    (ProfileContainer)