import React from 'react'
import Preloader from "../../common/preloader";
import logo512 from "./../../../logo512.png";
import s from "./profileInfo.module.css"
import ProfileStatus from "./ProfileStatus"
import ProfileStatusWithHook from './ProfileStatusWithHook';

class ProfileInfo extends React.Component {



    render() {
        return (
            <div>
                <div className={s.name}>
                    <div>{this.props.profile && this.props.profile.fullName}</div>
                </div>
                <div className={s.blockImg}>
                    {!this.props.profile ?
                        <Preloader /> :
                        !this.props.profile.photos.large ?
                            <img src={logo512} className={s.img} /> :
                            <img src={this.props.profile.photos.large} className={s.img} />}
                </div>
                <ProfileStatusWithHook status={this.props.status} updateStatus={this.props.updateStatus} />
                <div className={s.Description}>
                    {`Описание:
                    ${!this.props.profile ? <div>null</div> : this.props.profile.aboutMe}`}
                </div>
            </div>
        )
    }
}

