import s from './post.module.css'
import logo512 from './../../../../Assets/images/logo512.png'

const Post = (props) => {
    return (
        <div className={s.item}>
            <img className={`${s.img}`} src={logo512} />
            {props.message}
            <div><span>{`${props.likes} likes`}</span></div>
        </div>
    )
}

export default Post;